body {
  color: #333;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.header {
  background-color: #fff;
  border-bottom: 2px solid #e0e0e0;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  display: flex;
  box-shadow: 0 4px 8px #0000001a;
}

.navbar {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.nav-items > ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-items > ul > li {
  cursor: pointer;
  border-radius: 5px;
  margin-left: 30px;
  padding: 12px 18px;
  font-size: 16px;
  font-weight: 500;
  transition: background-color .3s, transform .3s;
  position: relative;
}

.nav-items > ul > li:hover {
  color: #fff;
  background-color: #007bff;
  transform: translateY(-3px);
}

.nav-items > ul > li:active {
  background-color: #0056b3;
  transform: translateY(1px);
}

.logo {
  width: 220px;
  transition: transform .3s;
}

.logo:hover {
  transform: scale(1.1);
}

.search-bar {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 20px;
  align-items: center;
  margin-left: 40px;
  padding: 8px 16px;
  display: flex;
  box-shadow: 0 4px 12px #0000000d;
}

.search-bar input {
  border: none;
  border-radius: 10px;
  outline: none;
  width: 200px;
  padding: 8px;
  font-size: 16px;
  transition: width .3s;
}

.search-bar input:focus {
  width: 250px;
  box-shadow: 0 0 5px #007bff99;
}

.search-bar button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 50%;
  padding: 8px 12px;
  transition: transform .2s;
}

.search-bar button:hover {
  background-color: #0056b3;
  transform: scale(1.1);
}

.card {
  vertical-align: top;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 12px;
  width: 250px;
  height: 380px;
  margin: 20px;
  padding: 16px;
  transition: transform .3s, box-shadow .3s;
  display: inline-block;
  box-shadow: 0 4px 12px #0000001a;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 24px #0000001a;
}

.card > img {
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  height: 200px;
  margin-bottom: 12px;
}

.RestaurantList {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin-top: 40px;
  padding: 0 10px;
  display: flex;
}

.RestaurantList .card {
  width: calc(25% - 20px);
}

@media (width <= 1200px) {
  .RestaurantList .card {
    width: calc(33.33% - 20px);
  }
}

@media (width <= 768px) {
  .RestaurantList .card {
    width: calc(50% - 20px);
  }

  .search-bar {
    width: 100%;
    margin-top: 20px;
    margin-left: 0;
  }
}

@media (width <= 480px) {
  .RestaurantList .card {
    width: 100%;
  }
}

.shimmer-card {
  background: #f0f0f0;
  border-radius: 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 250px;
  height: 380px;
  margin: 20px;
  display: flex;
  box-shadow: 0 4px 12px #0000000d;
}

.shimmer-image {
  background: linear-gradient(90deg, #e0e0e0 25%, #f8f8f8 50%, #e0e0e0 75%) 0 0 / 400% 100%;
  border-radius: 12px 12px 0 0;
  width: 100%;
  height: 200px;
  animation: 1.5s linear infinite shimmer;
}

.shimmer-line {
  background-color: #e0e0e0;
  border-radius: 4px;
  width: 80%;
  height: 10px;
  margin: 8px auto;
}

.shimmer-container {
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  display: flex;
}

@keyframes shimmer {
  0% {
    background-position: -400% 0;
  }

  100% {
    background-position: 400% 0;
  }
}

